.heading01 {
  width: 356px;
  height: 53px;
  background-image: url("../images/Logo.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.heading02 {
  font-size: 2.5rem;
  text-align: center;
  line-height: 1.2em;
  color: #fff;
  letter-spacing: 0.2em;
