/*==============================================================================

Animations SCSS.

==============================================================================*/
@keyframes animation-letters {
  0% {
    transform: scale(0);
  }
  50% {
    transform: scale(1.25);
  }
  100% {
    transform: scale(1);
  }
}

@keyframes animation-angle {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(10%);
  }
}

/*==============================================================================

FV SCSS.

==============================================================================*/
.header {
  width: 100%;
  height: 80px;
  background-color: rgba(255, 255, 255, 0.25);
  position: fixed;
  z-index: 100;
  box-shadow: 0 5.64px 16px 0 rgba(0, 0, 0, 0.4);
}

.header-inner {
  width: 100%;
  max-width: 1170px;
  height: 80px;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
}

@media (min-width: 991px) {
  .header-inner {
    justify-content: space-between;
  }
}

.anchor-wrapper {
  display: none;
  width: 50%;
  justify-content: space-around;
  align-items: center;
}

@media (min-width: 991px) {
  .anchor-wrapper {
    display: flex;
  }
}

.fv {
  background-image: url("../images/fv.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  width: 100%;
  height: 100vh;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.txt-heading02 {
  font-weight: bold;
  opacity: 0;
  transition: opacity 0.2s ease-in-out;
}

.fn-txt-heading02.is-active {
  opacity: 1;
  transition: opacity 0.2s ease;
}

.letters-wrapper {
  width: 100%;
  height: 50vh;
  position: absolute;
  bottom: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.letters {
  display: inline-block;
  padding: 10px 10px 10px 0;
  overflow: hidden;
}

.letters-inner {
  text-align: center;
  color: #fff;
  font-size: 1.375rem;
  line-height: 1.666666666666667em;
  letter-spacing: 0.1em;
  text-shadow: 3.064px 2.571px 1px rgba(0, 0, 0, 0.4);
  background-color: rgba(0, 0, 0, 0.25);
  padding: 20px;
  border-radius: 3px;
  box-shadow: 2.66px 3.43px 5px 0 rgba(0, 0, 0, 0.4);
  transform: translateX(-110%);
  transition: all 0.4s ease-out;
}

@media (orientation: landscape) {
  .letters-inner {
    padding: 10px;
    font-size: 1rem;
    line-height: 1.2em;
    margin: 35px 0 0;
  }
}

@media (min-width: 768px) {
  .letters-inner {
    font-size: 1rem;
  }
}

@media (min-width: 768px) and (orientation: landscape) {
  .letters-inner {
    padding: 20px;
    line-height: 1.666666666666667em;
    margin: 0;
  }
}

.fn-letters-inner.is-active {
  transform: translateX(0);
  transition: all 0.4s ease-out;
}

.txt-letters {
  display: inline-block;
  transform: scale(0);
}

.fn-txt-letters.is-active {
  animation: animation-letters 0.5s;
  transform: scale(1);
}

.button-floating {
  width: 50px;
  height: 50px;
  background-color: rgba(255, 64, 129, 0.5);
  box-shadow: 11.491px 9.642px 16px 4px rgba(0, 0, 0, 0.4);
  position: fixed;
  bottom: 15px;
  right: 15px;
  border-radius: 100%;
  display: flex;
  justify-content: space-around;
  flex-direction: column;
  align-items: center;
  padding: 6px 0;
  cursor: pointer;
  opacity: 1;
  transform: scale(0);
  transition: all 0.5s;
}

@media (min-width: 480px) {
  .button-floating {
    width: 60px;
    height: 60px;
  }
}

@media (min-width: 768px) {
  .button-floating {
    width: 75px;
    height: 75px;
    bottom: 30px;
    right: 30px;
  }
}

.button-floating:hover {
  opacity: 0.8;
  transition: all 0.5s;
}

.button-floating.is-active {
  transform: scale(1);
  transition: all 0.5s;
}

.txt-scroll {
  font-size: 1rem;
  font-family: 'Source Sans Pro', sans-serif;
  font-weight: bold;
  color: #fff;
}

.icon-angle-down {
  display: inline-block;
  color: #fff;
  font-size: 2rem;
}

.icon-angle-down.is-active {
  animation: animation-angle 0.75s ease-in-out infinite;
}
