@charset "UTF-8";
/*==============================================================================

Animations SCSS.

==============================================================================*/
@keyframes animation-letters {
  0% {
    transform: scale(0);
  }

  50% {
    transform: scale(1.25);
  }

  100% {
    transform: scale(1);
  }
}
@keyframes animation-angle {
  0% {
    transform: translateY(0);
  }

  100% {
    transform: translateY(10%);
  }
}